/**
  * bootstrap-switch - Turn checkboxes and radio buttons into toggle switches.
  *
  * @version v3.3.4
  * @homepage https://bttstrp.github.io/bootstrap-switch
  * @author Mattia Larentis <mattia@larentis.eu> (http://larentis.eu)
  * @license Apache-2.0
  */
.bootstrap-switch {
    display: inline-block;
    direction: ltr;
    cursor: pointer;
    border-radius: 4px;
    border: 2px solid;
    border-color: $custom-toggle-background;
    background: $custom-toggle-background;
    position: relative;
    text-align: left;
    overflow: hidden;
    width: 75px !important;
    border-radius: 20px;
    line-height: 8px;
    z-index: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    vertical-align: middle;
    -webkit-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    -o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    .bootstrap-switch-container {
        display: inline-block;
        top: 0;
        border-radius: 4px;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        width: 100px !important;
        background: $custom-toggle-background;
    }
    .bootstrap-switch-handle-on,
    .bootstrap-switch-handle-off,
    .bootstrap-switch-label {
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        cursor: pointer;
        display: table-cell;
        vertical-align: middle;
        padding: 4px 12px;
        font-size: 14px;
        line-height: 20px;
    }
    .bootstrap-switch-handle-on,
    .bootstrap-switch-handle-off {
        text-align: center;
        z-index: 1;
        width: 50px !important;
    }
    .bootstrap-switch-handle-on.bootstrap-switch-primary,
    .bootstrap-switch-handle-off.bootstrap-switch-primary {
        color: theme-color("primary");
        background: $custom-toggle-background;
    }
    .bootstrap-switch-handle-on.bootstrap-switch-info,
    .bootstrap-switch-handle-off.bootstrap-switch-info {
        color: theme-color("info");
        background: $custom-toggle-background;
    }
    .bootstrap-switch-handle-on.bootstrap-switch-success,
    .bootstrap-switch-handle-off.bootstrap-switch-success {
        color: theme-color("success");
        background: $custom-toggle-background;
    }
    .bootstrap-switch-handle-on.bootstrap-switch-warning,
    .bootstrap-switch-handle-off.bootstrap-switch-warning {
        color: theme-color("warning");
        background: $custom-toggle-background;
    }
    .bootstrap-switch-handle-on.bootstrap-switch-danger,
    .bootstrap-switch-handle-off.bootstrap-switch-danger {
        color: theme-color("danger");
        background: $custom-toggle-background;
    }
    .bootstrap-switch-handle-on.bootstrap-switch-default{
        color: theme-color("success");
        background: $custom-toggle-background;
    }
    .bootstrap-switch-handle-off.bootstrap-switch-default {
        color: theme-color("danger");
        background: $custom-toggle-background;
    }
    .bootstrap-switch-label {
        text-align: center;
        margin-top: -1px;
        margin-bottom: -1px;
        z-index: 100;
        color: #333;
        background: $white;
        border-radius: 20px;
    }
    span::before {
        content: "\200b";
    }
    input {
        &[type='radio'],
        &[type='checkbox'] {
            position: absolute !important;
            top: 0;
            left: 0;
            margin: 0;
            z-index: -1;
            opacity: 0;
            filter: alpha(opacity=0);
            visibility: hidden;
        }
    }
    &.bootstrap-switch-mini {
        .bootstrap-switch-handle-on,
        .bootstrap-switch-handle-off,
        .bootstrap-switch-label {
            padding: 1px 5px;
            font-size: 12px;
            line-height: 1.5;
        }
    }
    &.bootstrap-switch-small {
        .bootstrap-switch-handle-on,
        .bootstrap-switch-handle-off,
        .bootstrap-switch-label {
            padding: 5px 10px;
            font-size: 12px;
            line-height: 1.5;
        }
    }
    &.bootstrap-switch-large {
        .bootstrap-switch-handle-on,
        .bootstrap-switch-handle-off,
        .bootstrap-switch-label {
            padding: 6px 16px;
            font-size: 18px;
            line-height: 1.3333333;
        }
    }
    &.bootstrap-switch-disabled,
    &.bootstrap-switch-readonly,
    &.bootstrap-switch-indeterminate {
        cursor: default !important;
    }
    &.bootstrap-switch-disabled .bootstrap-switch-handle-on,
    &.bootstrap-switch-readonly .bootstrap-switch-handle-on,
    &.bootstrap-switch-indeterminate .bootstrap-switch-handle-on,
    &.bootstrap-switch-disabled .bootstrap-switch-handle-off,
    &.bootstrap-switch-readonly .bootstrap-switch-handle-off,
    &.bootstrap-switch-indeterminate .bootstrap-switch-handle-off,
    &.bootstrap-switch-disabled .bootstrap-switch-label,
    &.bootstrap-switch-readonly .bootstrap-switch-label,
    &.bootstrap-switch-indeterminate .bootstrap-switch-label {
        opacity: 0.5;
        filter: alpha(opacity=50);
        cursor: default !important;
    }
    &.bootstrap-switch-animate .bootstrap-switch-container {
        -webkit-transition: margin-left 0.5s;
        -o-transition: margin-left 0.5s;
        transition: margin-left 0.5s;
    }
    &.bootstrap-switch-inverse {
        .bootstrap-switch-handle-on {
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
            border-bottom-right-radius: 3px;
            border-top-right-radius: 3px;
        }
        .bootstrap-switch-handle-off {
            border-bottom-right-radius: 0;
            border-top-right-radius: 0;
            border-bottom-left-radius: 3px;
            border-top-left-radius: 3px;
        }
    }
    &.bootstrap-switch-focused {
        border-color: #66afe9;
        outline: 0;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
    }
}