.accordion{
    .accordion-wrapper-header {
        .btn{
            width: 100%;
            text-align: left;
            text-decoration: none;
            border-radius: 0;
            border: $border-width solid $border-color;
            position: relative;
            padding: $accordion-padding-y $accordion-padding-x;
            background: $white;
            color: $primary;
            &.collapsed{
                &:before{
                    transform: rotate(180deg);
                }
            }
            &:before{
                position: absolute;
                content: "\e627";
                font-family: 'themify';
                right: 20px;
                transition: $transition-base;
            }
        }
    }
    .accordion-wrapper-body{
        p{
            border-width: 0 $border-width $border-width $border-width;
            border-color: $border-color;
            border-style: solid;
            padding: $accordion-padding-y $accordion-padding-x;
            margin-bottom: 0;
        }
    }
}